<template>
  <div class="homework-container">
    <div class="publish-homework">
      <ul class="listview listviews listview-form">
        <li class="subject-li" @click="goHomeworkSubject">
          <label style="margin-right: 0.6rem">作业科目</label>
          <input  maxlength="30" style="border-bottom: none" id="leave-type" v-model="getSubject" type="text" placeholder="请选择作业科目"
                 readonly/>
          <img src="../assets/arrow-icon.png"/>
          <p></p>
        </li>
        <p class="tit">内容</p>
        <div class="txt">
          <textarea @input="validateInput" v-on:input="toTextNum()" @keyup="textBlur" v-model="textVal" placeholder="请输入作业内容"></textarea>
          <div class="float current-txt">
            <div class="txt-num"><span>{{remarkNum}}</span>/1000</div>

          </div>
          <!--上传组件start-->
          <v-upload-images  v-model="imgList"
                           @handle-uploaded-status="handleUploadedStatus"></v-upload-images>
          <!--上传组件end-->
        </div>
        <li class="mar-top" style="border:none" @click="goRange">
          <label style="margin-right: 0.6rem">发布范围</label>
          <input @input="validateInput" class="rangText" style="border-bottom: none" v-model="getHomeRangeValue" type="text" placeholder="请选择发布范围" readonly/>
          <img src="../assets/arrow-icon.png"/>
          <p></p>
        </li>
        <li style="padding-right: 0" class="mar-top receipt-li" >
          <v-switch  label="需回执" @input="getStatus" v-model="isReceive"></v-switch>
        </li>
        <!--回执截止时间-->
        <li v-show="receiveTime" class="bor" @click="openPicker('2')">
          <!--<label>选择日期</label>-->
          <span class="select-text">选择日期</span>
          <!--<input style="border-bottom: none" class="time-input" @input="validateInput" type="text" v-model="endTime" readonly />-->
          <span class="select-date-sp">{{endTime}}</span>
          <img src="../assets/arrow-icon.png"/>
          <p class="select-txt">选填</p>
        </li>
      </ul>
    </div>
    <v-datetime-picker
      ref="picker5"
      type="smart"
      v-model="value5"
      :visibleItemCount="5"
      @confirm="handlePicker5">
    </v-datetime-picker>
    <div class="footer-btn" v-if="!canSubmit">
      <button >发布</button>
    </div>
    <div class="footer-btn" v-if="canSubmit" @click="confirm">
      <button :class="{'btn': canSubmit}">发布</button>
    </div>
    <!--发布成功弹框-->
    <div v-show="shoadowShow" class="shadow">
      <em></em>
      <p>发布成功</p>
    </div>
  </div>
</template>

<script>
  import vCell from '../vendor/v-cell.vue';
  import vSwitch from '../vendor/v-switch.vue';
  import vDatetimePicker from '../vendor/v-datetime-picker';
  import vLabel from '../vendor/v-label.vue';
  import logger from '../js/utils/logger';
  import utils from '../js/utils/utils';
  import vUploadImages from '../vendor/v-upload-images.vue';
  import { mapGetters, mapMutations } from 'vuex';
  import Toast from '../vendor/v-toast';
  import api from '../utils/api';
  export default {
    name: 'publish-homework',
    components: {'v-cell': vCell, 'v-switch': vSwitch, 'v-label': vLabel, vDatetimePicker, 'v-upload-images': vUploadImages},
    data () {
      return {
        isRelease: false, // 限制多次提交
        isPublish: false, // 表示是否已经发布
        isReceive: false, // 回执状态
        value5: '',
        textVal: '',
        remarkNum: 0,
        newImgList: [], // 取npUrl
        imgList: [],
        showTag: !this.disableAdd && !this.lazyTag,
        uploadedStatus: this.uploaded,
        shoadowShow: false, // 提示弹窗
        teacherSubjectList: [], // 普通老师
        masterSubjectList: [], // 班主任
        teacherList: [], // 展示的普通老师科目
        masterList: [], // 展示的班主任科目
        receiveTime: false, // 回执时间是否显示
        endTime: '' // 选择截止日期
      };
    },
    created () {
      this.getSubjects();
      this.SET_HOME_RANGE_VALUE('');
    },
    mounted () {
      // logger.log('upload-create mounted... ');
    },
    activated () {
      this.isRelease = false;
      this.isPublish = !this.getHomeRangeValue.length;
      // 获取当前发布页是从哪个页面过来的
      this.$route.query.origin = this.getOriginType;
      if (this.$route.query.origin === 'listPage') {
        this.getSubjects();
        this.textVal = ''; // 清空作业内容
        this.imgList = []; // 清空图片内容
        this.remarkNum = 0; // 重置当前文字个数
        this.SET_HOME_RANGE_VALUE(''); // 清空发布范围值
        this.isReceive = false; // 恢复回执按钮不选中
        this.receiveTime = false; // 隐藏回执时间
        this.endTime = '';
      }
    },
    watch: {
      textVal (val) {
        this.textVal = val.replace(/[^\u4e00-\u9fa5a-zA-Z\d\r\n,.，。]+/, '');
      }
    },
    computed: {
      ...mapGetters(['getSubject', 'getRange', 'getHomeRangeValue', 'getHomewImgList', 'getHomeworkContent', 'getOriginType']),
      canSubmit () {
        if (!this.getSubject && !this.textVal && !this.getHomeRangeValue) {
          return false;
        } else {
          return (this.getSubject && this.textVal && this.getHomeRangeValue);
        }
      }
    },
    methods: {
      ...mapMutations(['SET_SUBJECT_DATA', 'SET_HOME_RANGE_VALUE', 'SET_HOMEWORK_CONTENT_DATA']),
      /**
       * 获取科目信息
       */
      getSubjects () {
        api.getSubjects().then((res) => {
          console.log(res);
          let data = res.data.data;
          this.teacherSubjectList = data.teacherSubjectList;
          this.masterSubjectList = data.masterSubjectList;
          if (this.teacherSubjectList.length > 0 && this.masterSubjectList.length === 0) {
            this.teacherList.push(this.teacherSubjectList[0].subjectName);
            this.teacherList = this.teacherList.join('');
            for (const obj of this.teacherSubjectList) {
              this.masterList.push(obj.subjectName);
            }
          } else if (this.teacherSubjectList.length > 0 && this.masterSubjectList.length > 0) {
            for (const obj of this.masterSubjectList) {
              this.masterList.push(obj.subjectName);
            }
            this.teacherList.push(this.teacherSubjectList[0].subjectName);
            this.teacherList = this.teacherList.join('');
          } else if (this.teacherSubjectList.length === 0 && this.masterSubjectList.length > 0) {
            for (const obj of this.masterSubjectList) {
              this.masterList.push(obj.subjectName);
            }
            this.teacherList = '';
          }
          console.log(this.teacherList);
          console.log(this.masterList);
          this.SET_SUBJECT_DATA(this.teacherList);
        }, (err) => {
          console.log(err);
        });
      },
      // textarea 去除特殊字符及表情
      textBlur () {
        let val = this.textVal;
        // this.textVal = val.replace(/\uD83C[\uDF00-\uDFFF]|\uD83D[\uDC00-\uDE4F]/g, '');
        this.textVal = val.replace(/[^\u4e00-\u9fa5a-zA-Z\d\r\n,.，。]+/, '');
      },
      // 范围
      goRange () {
        this.$router.push({path: '/searchResult', query: {'pushType': 1, 'isPublish': this.isPublish, 'noType': this.$route.query.noType}});
      },
      // 标题
      goHomeworkSubject () {
        this.$router.push({path: '/homeworkSubject', query: ''});
      },
      handleUploadedStatus (val) {
        logger.log('upload.handleUploadedStatus: ', val);
        this.uploaded = val;
      },
      toTextNum () {
        let vm = this;
        if (vm.textVal.length >= 1000) {
          vm.textVal = vm.textVal.substring(0, 1000);
        }
        vm.remarkNum = vm.textVal.length;
      },
      validateInput () {
      },
      openPicker (is) {
        this.value5 = new Date();
        this.$refs.picker5.open();
        this.startOrEnd = is;
      },
      handlePicker5 (value) {
        this.value5 = value;
        this.endTime = utils.formatTime(value.getTime(), 'yyyy-MM-dd hh:mm');
      },
      // 是否回执按钮
      getStatus (val) {
        console.log(val);
        if (val === true) {
          this.receiveTime = true;
        } else {
          this.receiveTime = false;
        }
      },
      // 确认
      confirm () {
        let vm = this;
        if (this.isRelease) {
          return;
        }
        this.isRelease = true;
        if (!vm.canSubmit) {
          return;
        }
        // const timestamp = (new Date(this.endTime)).valueOf(); // 时间戳转换的
        const timestamp = +new Date(this.endTime.replace(/-/g, '/')); // 时间戳转换的 兼容ios
        // 图片npUrl
        vm.newImgList = [];
        for (var i = 0;i < vm.imgList.length;i++) {
          if (vm.imgList[i].url) {
            let obj = {};
            obj.npUrl = vm.imgList[i].url;
            vm.newImgList.push(obj);
          }
        }
        let receiveStatus = 0;
        this.isReceive ? receiveStatus = 1 : receiveStatus = 0;
        // console.log(vm.newImgList, '99999999');
        let param = {
          notice: {
            noContent: vm.textVal,
            // noPicture: 0,
            noReceipt: receiveStatus,
            noPublisherId: '',
            noReceiptTime: timestamp,
            noTitle: '',
            noType: this.$route.query.noType,
            noSubject: this.getSubject,
            noExamId: '',
            noRanking: 0
          },
          noticePictures: vm.newImgList,
          noticeRanges: vm.getRange
        };
        if (vm.imgList.length > 0) {
          param.notice.noPicture = 1;
        } else {
          param.notice.noPicture = 0;
        }
        if (!vm.getSubject) Toast('请选择作业科目');
        if (!vm.textVal) Toast('请输入作业内容');
        if (!vm.getHomeRangeValue) Toast('请选择发布范围');
        api.publishNotice(JSON.stringify(param)).then((res) => {
          logger.log(res, ''); // 发布作业
          vm.shoadowShow = true;
          setTimeout(() => {
            vm.shoadowShow = false;
            // 每次发布成功后  不重置作业科目
            // this.SET_SUBJECT_DATA('');
            this.SET_HOME_RANGE_VALUE('');
            vm.textVal = '';
            vm.imgList = [];
            vm.endTime = '';
            vm.isReceive = false;
            vm.$router.replace({path: '/'});
          }, 500);
        }).catch((err) => {
          this.isRelease = true;
          logger.log(err);
        });
      }
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "../scss/variables";
  @import "../scss/mixins";
  body {
    background: #f2f2f4;
  }
  .homework-container{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #f2f2f4;
  }
  .publish-homework {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
  }

  ul {
    height: auto;
    overflow-y: scroll;
    /*padding: 15px;*/
    .mar-top {
      margin-top: 10px;
    }
    .receipt-li{
      /*border-bottom: #e1e1e1 1px solid;!*no*!*/
    }
    .rangText{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
    .tit {
      padding-left: 15px;
      height: 37px;
      line-height: 37px;
      font-size: 14px;
      color: #777E8C;
    }
    .txt {
      width: 100%;
      min-height:284px;
      padding: 15px;
      box-sizing: border-box;
      /*border-top: 1px solid #e1e1e1; !*no*!*/
      /*border-bottom: 1px solid #e1e1e1; !*no*!*/
      background: #FFFFFF;
      position: relative;
      textarea {
        width: 100%;
        height: 126px;
        font-size:15px;
        line-height: 21px;
      }
      .float{
        width:100%;
        height:20px;
      }
      .current-txt{
        margin-top: 22px;
      }
      .txt-num {
        float: right;
        font-size: pxTorem(14px);
        line-height: pxTorem(14px);
        color: #777E8C;
        span {
          font-size: pxTorem(14px);
          line-height: pxTorem(14px);
        }
      }
      .height-auto {
        width: 100%;
        height: auto;
        position: absolute;
        top: 89px;
        left: 15px;
      }
    }
    li {
      padding-left: 15px;
      width: 100%;
      height: 44px;
      line-height: 44px;
      position: relative;
      box-sizing: border-box;
      label {
        width: 100%;
        /*display: inline-block;*/
        font-size: pxTorem(15px);
        line-height: pxTorem(15px);
        color: #383838;
      }
      input {
        display: inline-block;
        /*width: 65%;*/
        height: pxTorem(44px);
        border-bottom: none !important;
        /*padding: pxTorem(13px) pxTorem(23px) pxTorem(13px) 10px;*/
        box-sizing: border-box;
        font-size: pxTorem(15px);
        line-height: pxTorem(16px);
      }
      img {
        width: pxTorem(6px);
        height: pxTorem(10px);
        position: absolute;
        right: 15px;
        top: pxTorem(19px);
      }
      p {
        width: pxTorem(285px);
        height: pxTorem(1px);
        /*border-bottom: 1px solid #e1e1e1;*/
        position: absolute;
        bottom: 0;
        right: 0;
      }

    }
    li:first-child {
      border-bottom: 1px solid #e1e1e1;/*no*/
      input {
        border-bottom: 0;
      }
    }
  }
  .v-upload-images__frm .v-upload-images__btn{
    width:80px!important;
    height: 80px !important;
  }
  .v-upload-images__img.icon{
    font-size:22px !important;
    width: 80px;
    height:80px;
  }
  // 十字图标
  .v-upload-images__img .icon .icon-add{
    width:22px!important;
    height:22px!important;
  }
  .v-upload-images__img{
    width: 80px!important;
    height:80px!important;
  }
  .v-upload-images__frm{
    margin-left: 0!important;
  }
  .v-upload-images__con > div{
    margin-left: 0;
    width:80px!important;
    height:80px!important;
    position: relative!important;

  }
  .v-upload-images__con{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .v-upload-images__del {
    position: absolute!important;
    top: 0px!important;
    right: 0px!important;
  }
  .v-cell > div {
    border-bottom: 1px solid #e1e1e1;/*no*/
  }
  .el-checkbox__input.is-indeterminate .el-checkbox__inner{
    border-color: rgba(0,0,0,0.1)!important;
  }
  .v-upload-images__frm.v-upload-images__btn{
    border:1px solid #e1e1e1!important;/*no*/
    margin-left: 0;
    width: 80px;
    height:80px;
  }
  .v-upload-images{
    margin-top:12px;
    z-index: 100;
    width: 100%;
    min-height: 100px;
    display:flex;
    /*justify-content: space-between;*/
  }
  /*.listview-form li:last-child{*/
  /*border-bottom:1px solid #e1e1e1!important;*/
  /**/
  /*}*/
  /*.listview-form li:first-child{*/
  /*border-bottom:1px solid #e1e1e1!important;*/
  /*}*/
  .listview-form li:first-child {
    border: none;
  }

  .listview-form li:last-child {
    border: none;
  }
  .shadow {
    width: 120px;
    height: 120px;
    background: #000000;
    opacity: 0.5;
    border-radius: 5px;
    margin: -405px auto;
    em {
      margin: 15px 0 0 35px;
      /*padding:15px 35px 0 35px;*/
      display: inline-block;
      width: 50px;
      height: 50px;
      background: url('../assets/toast-rig.png') no-repeat center;
      background-size: 100%;
    }
    p {
      margin-left: 35px;
      font-size: 13px;
      color: #FFFFFF;
      letter-spacing: 0;
    }
  }
  /*v-switch组件*/


  .v-switch__input {
    display: block;
  }
  .v-switch__core {
    width:41px!important;
    height:25px!important;
  }
  .v-switch__core:after{
    width: 0.6rem!important;
    height:0.6rem!important;
  }
  /*v-switch组件*/
  .v-switch__label {
    height: 100%;
    font-size: pxTorem(15px);
    line-height: 44px;
    color: #383838;
    padding-left: 0 !important;
    padding-right: 260px!important;
  }
  .v-switch__input {
    display: block;
  }
  .v-cell__wrapper {
    margin-left: 0 !important;
  }
  .listviews {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding-bottom: 76px;
  }
  .listview-form li > div {
    border-bottom: 1px solid #e1e1e1;/*no*/
  }

  .v-cell > div {
    border-bottom: 1px solid #e1e1e1;/*no*/
  }
  .footer-btn {
    width: 100%;
    height: pxTorem(54px);
    padding: 8px 30px;
    box-sizing: border-box;
    background: #FFFFFF;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background: rgba(255,255,255,0.90);
    box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
    border-top: 1px solid #e1e1e1;/*no*/
    z-index: 9;
    .btn {
      background: #C3303A;
    }
    button {
      width: 315px;
      height: 38px;
      font-size: pxTorem(17px);
      line-height: pxTorem(38px);
      text-align: center;
      color: #FFFFFF;
      background: #e1e1e1;
      border-radius: pxTorem(4px);
    }

  }
  .bor {
    padding-right: 0;
    border-top: 1px solid #e1e1e1;/*no*/
    border-bottom: 1px solid #e1e1e1;/*no*/
    .time-input{
      padding-left: 18px;
    }
    .select-text{
      padding-top: 2px;
      float: left;
      width: 20%;
      font-size: pxTorem(15px);
      height: 43px;
      line-height: 43px;
      color: #383838;
    }
    .select-date-sp{
      padding-top: 2px;
      padding-left: 13px;
      display: inline-block;
      width: 65%;
      height: pxTorem(43px);
      line-height: pxTorem(43px);
      border-bottom: none !important;
      /*padding: pxTorem(13px) pxTorem(23px) pxTorem(13px) 10px;*/
      box-sizing: border-box;
      font-size: pxTorem(15px);
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
  }
  .select-txt {
    position: absolute;
    top: 16px;
    left: 315px;
    font-size: 15px;
    width: auto;
    color: #777E8C;
    line-height: 15px;
  }
</style>



